import React, { useRef, useEffect } from 'react';
import { Canvas, useFrame } from '@react-three/fiber';
import { OrbitControls, Stars, Sky as ThreeSky } from '@react-three/drei';
import * as SunCalc from 'suncalc';

/**
 * Componente 3D que renderiza o Sol e o C√©u com base na posi√ß√£o solar
 */
const SkyWithSun = ({ latitude, longitude }) => {
  const sunRef = useRef();
  const skyRef = useRef();

  useEffect(() => {
  const handleOrientation = (e) => {
    if (sunRef.current) {
      // Ajusta a posi√ß√£o do sol com base no movimento do dispositivo
      const beta = e.beta * 0.01;  // Inclina√ß√£o frontal (eixo X)
      const gamma = e.gamma * 0.01; // Inclina√ß√£o lateral (eixo Y)
      sunRef.current.position.y += beta;
      sunRef.current.position.x += gamma;
    }
  };

  window.addEventListener('deviceorientation', handleOrientation);
  return () => window.removeEventListener('deviceorientation', handleOrientation);
}, []);

  // Atualiza a posi√ß√£o do Sol com base no hor√°rio e localiza√ß√£o
  useFrame(() => {
    const now = new Date();
    const sunPos = SunCalc.getPosition(now, latitude, longitude);

    // Converter coordenadas solares (azimute e altitude) para posi√ß√£o 3D
    const distance = 100; // Dist√¢ncia do sol (ajuste conforme necess√°rio)
    const x = distance * Math.cos(sunPos.altitude) * Math.sin(sunPos.azimuth);
    const y = distance * Math.sin(sunPos.altitude);
    const z = distance * Math.cos(sunPos.altitude) * Math.cos(sunPos.azimuth);

    if (sunRef.current) {
      sunRef.current.position.set(x, y, z);
    }

    // Ajusta a cor do c√©u conforme a posi√ß√£o do sol
    if (skyRef.current) {
      const isDay = sunPos.altitude > 0;
      skyRef.current.material.uniforms.sunPosition.value.set(x, y, z);
      skyRef.current.material.uniforms.turbidity.value = isDay ? 2 : 10; // C√©u mais limpo de dia
      skyRef.current.material.uniforms.rayleigh.value = isDay ? 1 : 3; // Atmosfera mais azul de dia
    }
  });

  return (
    <>
      {/* C√©u din√¢mico */}
      <ThreeSky
        ref={skyRef}
        distance={1000}
        sunPosition={[0, 0, 0]}
        turbidity={2}
        rayleigh={1}
        mieCoefficient={0.005}
        mieDirectionalG={0.8}
      />

      {/* Sol (esfera luminosa) */}
      <mesh ref={sunRef}>
        <sphereGeometry args={[5, 32, 32]} />
        <meshBasicMaterial color="#FFD700" />
        <pointLight intensity={1.5} color="#FFD700" />
      </mesh>
    </>
  );
};

/**
 * Componente principal que renderiza o Canvas 3D
 */
export const SkyBackground = ({ latitude, longitude }) => {
  return (
    <div style={{ position: 'fixed', top: 0, left: 0, width: '100%', height: '100%', zIndex: -1 }}>
      <Canvas camera={{ position: [0, 0, 50], fov: 75 }}>
        <ambientLight intensity={0.1} />
        <SkyWithSun latitude={latitude} longitude={longitude} />
        <OrbitControls enableZoom={false} enablePan={false} enableRotate={false} />
      </Canvas>
    </div>
  );
};